const productsBtn = document.querySelectorAll('.product-card__btn');
const productsItemBtn = document.querySelector('.product-item__btn');
const cartProductsList = document.querySelector('.basket__list');
const cartOrderList = document.querySelector('.side-block__list');
const cart = document.querySelector('.cart');
const cartQuantity = cart.querySelector('.cart__quantity');
const orderModalOpenProd = document.querySelector('.order-modal__btn');
const orderModalList = document.querySelector('.order-modal__list');
let fullPrice = document.querySelector('.fullprice');
let itemPrice = document.querySelectorAll('.item-price');
let price = 0;
let productArray = [];

const priceWithoutSpaces = (str) => {
  return str.replace(/\s/g, '');
};

const normalPrice = (str) => {
  return String(str).replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ');
};

const printQuantity = () => {
  let countQuantity = 0;
  if (productArray.length > 0){
    productArray.forEach (el => {
      countQuantity += el.quantity;
    });
  }
  if (countQuantity > 0 ){
    cartQuantity.textContent = countQuantity;
    cartQuantity.classList.remove('cart__quantity--empty');
  }else{
    cartQuantity.classList.add('cart__quantity--empty');
  }
};

function generateCartProduct (id, quantity, img, title, price) {
  return `
  <li class="basket-item" data-id="${id}">
    <div class="basket-item__img">
      <img src="${img}" alt="">
    </div>
    <a href="product.html" class="basket-item__title">${title}</a>
    <div class="basket-item__number">
      <button class="btn-reset basket-item__btn basket-item__btn--subtract">-</button>
      <input class="basket-item__value input-reset" type="text" name="name" value="${quantity} ">
      <button class="btn-reset basket-item__btn basket-item__btn--add">+</button>
    </div>
    <div class="basket-item__price"><span class="item-price">${normalPrice(price * quantity)}</span> ₽</div>
    <button class="btn-reset basket-item__btn basket-item__btn--del"></button>
  </li>
  `;
};

function generateOrderProduct (id, quantity, img, title, price) {
  return `
  <li class="side-block__item side-item" data-id="${id}">
    <div class="side-item__title">${title} — x${quantity}</div>
    <div class="side-item__info">${normalPrice(price * quantity)} ₽</div>
  </li>
  `;
};

productsBtn.forEach(el => {

  el.addEventListener('click', (el) => {
    let self = el.currentTarget;
    let parent = self.closest('.product-card');
    let id = parent.dataset.id;
    let img = parent.querySelector('.product-card__img img').getAttribute('src');
    let title = parent.querySelector('.product-card__title').textContent;
    let priceString = priceWithoutSpaces(parent.querySelector('.product-card__price span').textContent);
    let priceNum = priceWithoutSpaces (parent.querySelector('.product-card__price span').textContent);

    addProduct (id, img, title, priceString);
    parent.classList.add('notification--active');
    setTimeout(function(){
      parent.classList.remove('notification--active');
    }, 600);
    self.innerHTML = 'Добавить еще';

  });
});

if(productsItemBtn){
  productsItemBtn.addEventListener('click', (el) => {
    let self = el.currentTarget;
    let parent = self.closest('.product-item');
    let id = parent.dataset.id;
    let img = parent.querySelector('.product-item__img img').getAttribute('src');
    let title = document.querySelector('.product__title').textContent;
    let priceString = priceWithoutSpaces(parent.querySelector('.product-item__price').textContent);
    let priceNum = priceWithoutSpaces (parent.querySelector('.product-item__price').textContent);

    addProduct (id, img, title, priceString);
    parent.classList.add('notification--active');
    setTimeout(function(){
      parent.classList.remove('notification--active');
    }, 600);
    self.innerHTML = 'Добавить еще';
  });
}

function addProduct (id, img, title, price) {
  let obj = {};
  obj.title = title;
  obj.price = price;
  obj.id = id;
  obj.img = img;
  obj.quantity = 1;

  if (productArray.find(item => item.id == id)) {
    productArray.find(item => item.id === id).quantity += 1;
  }else{
    productArray.push(obj);
  }

  updateStorage();
  printQuantity();
  if (fullPrice){
    updatePrice();
  }
}

const countSumm = () => {
  document.querySelectorAll('.cart-content__item').forEach(el => {
    price += parseInt(priceWithoutSpaces(el.querySelector('.cart-product__price').textContent));
  });
};

const initialState = () => {
  if (localStorage.getItem('basket') !== null) {
    if (cartProductsList){
      cartProductsList.innerHTML;
    };

    updateBasket();
    printQuantity();
    if (cartProductsList) {
      productArray.forEach (el => {
        updatePrice();
        if (el.quantity > 0) {
          cartProductsList.insertAdjacentHTML('afterbegin', generateCartProduct(el.id, el.quantity, el.img, el.title, el.price));
        }
      });
    }
    if (cartOrderList) {
      productArray.forEach (el => {
        updatePrice();
        if (el.quantity > 0) {
          cartOrderList.insertAdjacentHTML('afterbegin', generateOrderProduct(el.id, el.quantity, el.img, el.title, el.price));
          printFullPrice();
        }
      });
    }

  }
};

initialState();

const plusBtn = document.querySelectorAll('.basket-item__btn--add');
const minusBtn = document.querySelectorAll('.basket-item__btn--subtract');
const cartProduct = document.querySelectorAll('.basket-item');
const delBtn = document.querySelectorAll('.basket-item__btn--del');
fullPrice = document.querySelector('.fullprice');
itemPrice = document.querySelectorAll('.item-price');

plusBtn.forEach(el => {
  el.addEventListener('click', (el) => {

    let self = el.currentTarget;
    let parent = self.closest('.basket-item');
    let id = parent.dataset.id;
    value = parent.querySelector('.basket-item__value');

    plusBasketValue(id);
    updateBasketValue (id, value);
    updateStorage();
    printQuantity();
    updatePrice();
  });
});

minusBtn.forEach(el => {
  el.addEventListener('click', (el) => {

    let self = el.currentTarget;
    let parent = self.closest('.basket-item');
    let id = parent.dataset.id;
    value = parent.querySelector('.basket-item__value');

    minusBasketValue(id);
    updateBasketValue (id, value);
    updateStorage();
    printQuantity();
    updatePrice();
  });
});

delBtn.forEach(el => {
  el.addEventListener('click', (el) => {
    let self = el.currentTarget;
    let parent = self.closest('.basket-item');
    let id = parent.dataset.id;
    deleteBasketProduct(id);

    updateStorage();
    printQuantity();
    updatePrice();
  });
});


function plusBasketValue(id) {
  productArray.find(item => item.id === id).quantity += 1;
}

function minusBasketValue(id) {
  quantity = productArray.find(item => item.id === id).quantity;
  if (quantity > 1){
    productArray.find(item => item.id === id).quantity -= 1;
  }else if(quantity === 1){
    deleteBasketProduct(id);
  }
}

function updateBasketValue(id, value) {
  value.setAttribute("value", productArray.find(item => item.id === id).quantity);
}

function deleteBasketProduct (id) {
  productArray.find(item => item.id === id).quantity = 0;

  cartProduct.forEach (el => {
    if(el.getAttribute('data-id') === id) {
      el.remove();
    }
  });
};

function updateStorage () {
  if (productArray.length) {
    localStorage.setItem('basket', JSON.stringify(productArray));
  } else {
    localStorage.removeItem('basket');
  }
};

function updateBasket () {
  productArray = readBasket();
};

function updatePrice() {
  result = 0;
  productArray.forEach (el => {
    result += (Number(el.price) * Number(el.quantity));
  });
  price = result;

  printItemPrice();
  printFullPrice();
}

function printFullPrice() {
  if (price) {
    fullPrice.textContent = `Итого: ${normalPrice(price)} ₽`;
  }else if (document.querySelector('.basket__wrapper')){
    document.querySelector('.basket__wrapper').textContent = ``;

    document.querySelector('.basket__wrapper').insertAdjacentHTML('afterbegin', printBasketEmpty());

  }
};

function printItemPrice () {
  itemPrice.forEach(el => {
    let parent = el.closest('.basket-item');
    let id = parent.dataset.id;
    if(parent.getAttribute('data-id') === id) {
      let itemQuantity = productArray.find(item => item.id === id).quantity;
      let itemPrice = productArray.find(item => item.id === id).price;
      el.textContent = normalPrice(itemQuantity * itemPrice);
    }
  });
}

function printBasketEmpty(){
  return `
  <div class="basket-empty">
  <h2 class="basket-empty__title">Корзина пуста</h2>
  <div class="basket-empty__message">Для заказа добавьте товары в корзину</div>
  <a href="#catalog-section" class="btn btn-reset basket-empty__btn">Перейти к товарам</a>
  </div>
  `;
}

function readBasket() {
  let basketJSON = localStorage.getItem("basket")
  if (basketJSON === null) {
    return undefined
  }

  try {
    return JSON.parse(basketJSON)
  } catch (e) {
    localStorage.removeItem("basket")
    return undefined
  }
}

